import React from 'react'
import Rate from '../../../../../Components/Media/rate.png'


function CircleRate3({data}) {
    return (
        <>
            <div className=''>
                <h1 className='px-2 font-semibold mt-4 text-gray-600 text-sm'><img className='inline w-6' src={Rate} alt="rate-image" /> Circle Rate 3 </h1>

                <table className='min-w-full leading-normal mt-2'>
                    <thead className='font-bold text-left text-sm bg-indigo-600 text-white'>
                        <tr>
                            <th className="px-2 py-3 border-b  text-xs capitalize text-center border-r border-l border-white" ></th>
                            <th className="px-2 py-3 border-b  text-xs capitalize text-center border-r border-l border-white" colSpan={2}>DLX Apartment (Square Feet )</th>
                            <th className="px-2 py-3 border-b  text-xs capitalize text-center border-r border-l border-white" colSpan={2}>Building Pakka (Square Feet )</th>
                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-center" colSpan={2}>Building Kaccha (Square Feet)</th>
                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-center" colSpan={2}>Vacant Land (Square Feet )</th>

                        </tr>
                        <tr>
                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Usage Type</th>

                            <th className="px-2 py-3 border-b   text-xs border border-white capitalize text-left">Main Road</th>
                            <th className="px-2 py-3 border-b  text-xs capitalize text-left ">Others</th>

                            <th className="px-2 py-3 border-b   text-xs border border-white capitalize text-left">Main Road</th>
                            <th className="px-2 py-3 border-b  text-xs capitalize text-left ">Others</th>
                            
                            <th className="px-2 py-3 border-b   text-xs border border-white capitalize text-left">Main Road</th>
                            <th className="px-2 py-3 border-b  text-xs capitalize text-left ">Others</th>

                            <th className="px-2 py-3 border-b   text-xs border border-white capitalize text-left">Main Road</th>
                            <th className="px-2 py-3 border-b  text-xs capitalize text-left ">Others</th>

                        </tr>
                    </thead>
                    <tbody className="text-sm">
                            <tr className="bg-white shadow-lg border-b border-gray-200">
                                <td className="px-2 py-2 text-sm text-left ">Residential</td>


<td className="px-2 py-2 text-sm text-left border-l border-gray-500">{data?.DLX_APARTMENT?.filter(item => (item?.usage_type === "RESIDENTIAL" && item?.road_type_mstr_id == 1))[0]?.rate}</td>
                                <td className="px-2 py-2 text-sm text-left  border-gray-500">{data?.DLX_APARTMENT?.filter(item => (item?.usage_type === "RESIDENTIAL" && item?.road_type_mstr_id == 0))[0]?.rate}</td>

                                <td className="px-2 py-2 text-sm text-left border-l border-gray-500">{data?.BUILDING_PAKKA?.filter(item => (item?.usage_type === "RESIDENTIAL" && item?.road_type_mstr_id == 1))[0]?.rate}</td>
                                <td className="px-2 py-2 text-sm text-left  border-gray-500">{data?.BUILDING_PAKKA?.filter(item => (item?.usage_type === "RESIDENTIAL" && item?.road_type_mstr_id == 0))[0]?.rate}</td>

                                <td className="px-2 py-2 text-sm text-left border-l border-gray-500">{data?.BUILDING_KACCHA?.filter(item => (item?.usage_type === "RESIDENTIAL" && item?.road_type_mstr_id == 1))[0]?.rate}</td>
                                <td className="px-2 py-2 text-sm text-left  border-gray-500">{data?.BUILDING_KACCHA?.filter(item => (item?.usage_type === "RESIDENTIAL" && item?.road_type_mstr_id == 0))[0]?.rate}</td>

                                <td className="px-2 py-2 text-sm text-left border-l border-gray-500">{data?.VACCANT_LAND?.filter(item => (item?.usage_type === "RESIDENTIAL" && item?.road_type_mstr_id == 1))[0]?.rate}</td>
                                <td className="px-2 py-2 text-sm text-left  border-gray-500">{data?.VACCANT_LAND?.filter(item => (item?.usage_type === "RESIDENTIAL" && item?.road_type_mstr_id == 0))[0]?.rate}</td>
                            </tr>

                            <tr className="bg-white shadow-lg border-b border-gray-200">
                                <td className="px-2 py-2 text-sm text-left ">Commercial</td>

 
                                     <td className="px-2 py-2 text-sm text-left border-l border-gray-500">{data?.DLX_APARTMENT?.filter(item => (item?.usage_type === "COMMERCIAL" && item?.road_type_mstr_id == 1))[0]?.rate}</td>
                                <td className="px-2 py-2 text-sm text-left  border-gray-500">{data?.DLX_APARTMENT?.filter(item => (item?.usage_type === "COMMERCIAL" && item?.road_type_mstr_id == 0))[0]?.rate}</td>

                                <td className="px-2 py-2 text-sm text-left border-l border-gray-500">{data?.BUILDING_PAKKA?.filter(item => (item?.usage_type === "COMMERCIAL" && item?.road_type_mstr_id == 1))[0]?.rate}</td>
                                <td className="px-2 py-2 text-sm text-left  border-gray-500">{data?.BUILDING_PAKKA?.filter(item => (item?.usage_type === "COMMERCIAL" && item?.road_type_mstr_id == 0))[0]?.rate}</td>

                                <td className="px-2 py-2 text-sm text-left border-l border-gray-500">{data?.BUILDING_KACCHA?.filter(item => (item?.usage_type === "COMMERCIAL" && item?.road_type_mstr_id == 1))[0]?.rate}</td>
                                <td className="px-2 py-2 text-sm text-left  border-gray-500">{data?.BUILDING_KACCHA?.filter(item => (item?.usage_type === "COMMERCIAL" && item?.road_type_mstr_id == 0))[0]?.rate}</td>

                                <td className="px-2 py-2 text-sm text-left border-l border-gray-500">{data?.VACCANT_LAND?.filter(item => (item?.usage_type === "COMMERCIAL" && item?.road_type_mstr_id == 1))[0]?.rate}</td>
                                <td className="px-2 py-2 text-sm text-left  border-gray-500">{data?.VACCANT_LAND?.filter(item => (item?.usage_type === "COMMERCIAL" && item?.road_type_mstr_id == 0))[0]?.rate}</td>


                            </tr>
                           
                        

                    </tbody>
                </table>
            </div>
        </>
    )
}

export default CircleRate3