import React from 'react'
import Rate from '../../../../../Components/Media/rate.png'

function RentalRate1({data}) {
    return (
        <>
            <div className='ml-10'>
                <h1 className='px-2 font-semibold mt-4 text-gray-600 text-sm'><img className='inline w-6' src={Rate} alt="rate-image" /> Rental Rate 1 </h1>

               
                    <table className='min-w-full leading-normal mt-2'>
                    <thead className='font-bold text-left text-sm bg-indigo-600 text-white'>
                        <tr>
                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Construction Type
                               <br/> ------------ <br/>
                                USE OF BUILDING</th>
                            <th className="px-2 py-3 border-b text-xs capitalize text-left border-l border-white">Pucca with Asbestos/Corrugated Sheet (ACC)</th>
                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Pucca with RCC Roof (RCC)</th>
                            <th className="px-2 py-3 border-b   text-xs capitalize text-left border-r border-white">Kuttcha with Clay Roof (Other)</th>
                        </tr>
                    </thead>
                     <tbody className="text-sm">
                      {
                    Object.entries(data)?.map(([key, value], index) =>   <>
                            <tr className="bg-white shadow-lg border-b border-gray-200">
                                <td className="px-2 py-2 text-sm text-left ">{key}</td>
                                <td className="px-2 py-2 text-sm text-left border-l border-gray-500">{value['Pucca with Asbestos/Corrugated Sheet (ACC)'][0]?.rate}</td>
                                <td className="px-2 py-2 text-sm text-left">{value['Pucca with RCC Roof (RCC)'][0]?.rate}</td>
                                <td className="px-2 py-2 text-sm text-left border-r border-gray-500">{value['Kuttcha with Clay Roof (Other)'][0]?.rate}</td>
                            </tr>
                        </>)}

                    </tbody>
                </table>
                
            </div>
        </>
    )
}

export default RentalRate1