

import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import axios from "axios";
import WaterApiList from "../../../Components/ApiList/WaterApiList";
import { useNavigate } from "react-router-dom";
import { ThreeDots } from "react-loader-spinner";
import { string } from "yup";
import BrandLoader from "../../../Components/Common/BrandLoader";
import CommonModal from "../../../Components/GlobalData/CommonModal";
import ServerErrorCard from "../../AdvertisementModule/ServerErrorCard";
import BottomErrorCard from "../../../Components/BottomErrorCard";
import { buttonActionSolid, buttonCancelBorder } from "../../../Components/Common/Styles/Styles";
import AxiosInterceptors from "../../../Components/GlobalData/AxiosInterceptors";

const customStyles1 = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    background: "transparent",
    border: "none",
    // transparent:"none"
  },
};

const customStyles = {
  overlay: {
    // background: "rgba(0, 0, 0, 0.5)",
    background: "#e6e6fc",
    // overflowY: "scroll"
  },
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    backgroundColor: "transparent",
    border: "none",
    height: "600px", //or maxHeight
  },
};

function SafWelcomeScreen(props) {
  // Modal.setAppElement('#yourAppElement');
  const [modalIsOpen, setIsOpen] = React.useState(false);
  const [ulbList, setUlbList] = useState();
  const [selectedUlb, setSelectedUlb] = useState(false);
  const [validULB, setValidULB] = useState(true);
  const [erroState, seterroState] = useState(false);
  const [isLoading, setisLoading] = useState(false);
  const [currentUlb, setcurrentUlb] = useState(null);
  const [erroMessage, seterroMessage] = useState(null);




  const { api_ulbList } = WaterApiList();

  const navigate = useNavigate();

  useEffect(() => {
    if (props.openUlbModal == 0) setIsOpen(true);
  }, [props.openUlbModal]);

  useEffect(() => {
    // axios
    //   .get(api_ulbList)
    //   .then(
    //     (res) => (console.log("ULB LIsts...", res), setUlbList(res.data.data))
    //   )
    //   .catch((err) => console.log("Error while fetching ulblist", err));
    fetchUlbList()
  }, [])

  const fetchUlbList = () => {
    setisLoading(true)
    AxiosInterceptors.get(api_ulbList)
      .then(function (response) {
        console.log("ULB LIsts inside change...", response)
        if (response?.data?.status) {
          setUlbList(response.data.data)
        }
        else {
          activateBottomErrorCard(true, 'Error occured. Please try again later')

        }
        setisLoading(false)
      })
      .catch(function (error) {
        activateBottomErrorCard(true, 'Error occured. Please try again later')
        setisLoading(false)
        console.log("Error while fetching ulblist", error)
      })
  }

  function afterOpenModal() { }

  function closeModal() {
    setIsOpen(false);
    navigate("/propertyDashboard");
    // setPaymentStatus(false);
  }

  const payNow = () => {
    if (!selectedUlb.value) {
      setValidULB(false);
    } else {
      setValidULB(true);
      console.log("Payment Button Cliked..");
      props.nextScreen();
      props.setSelectedUlb(selectedUlb);
      setIsOpen(false);
    }
  };

  const actionUlbSelection = () => {
    console.log('choose ulb id...', currentUlb)
    if (currentUlb === '' || currentUlb === null) {
      console.log('ulb not found please select')
      activateBottomErrorCard(true, 'Please select ULB')
      return
    }
    if (currentUlb != '2') {
      activateBottomErrorCard(true, "Apologies! The chosen ULB is temporarily inaccessible.")
      return
    }
    props?.setwelcomeScreenStatus(false)
    props?.setholdingCardStatus(true)
  }

  console.log("selectedUlb", selectedUlb.value, selectedUlb.text);

  const activateBottomErrorCard = (state, msg) => {
    seterroMessage(msg)
    seterroState(state)

  }

  if (isLoading) {
    return (
      <>
        <BrandLoader />
        <div className="h-screen w-full"></div>
      </>
    )
  }
  // if (erroState) {
  //   return (
  //     <CommonModal>
  //       <ServerErrorCard title="Server is busy" desc="Server is too busy to respond. Please try again later." buttonText="View Dashboard" buttonUrl="/propertyDashboard" />
  //     </CommonModal>
  //   )
  // }

  return (
    <div className="">
      {/* <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      > */}
      {erroState && <BottomErrorCard activateBottomErrorCard={activateBottomErrorCard} errorTitle={erroMessage} />}
      <div className="h-screen w-full"></div>
      <CommonModal>
        <div>
          <div class="relative w-full h-full max-w-3xl md:h-auto border-indigo-600 shadow-indigo-200 shadow-xl rounded">
            <div class="relative bg-white rounded-lg shadow ">
              <div class="flex items-start justify-between p-4 md:border-b rounded-t darks:border-gray-600">
                <h3 class="text-xl font-semibold text-gray-900 darks:text-white">
                  {props?.safType == 'new' && 'You are applying for new assessment'}
                  {props?.safType == 're' && 'You are applying for re assessment'}
                  {props?.safType == 'mu' && 'You are applying for mutation'}
                  {props?.safType == 'bi' && 'You are applying for bifurcation'}
                  {props?.safType == 'am' && 'You are applying for amalgamation'}
                </h3>
                <button
                  onClick={() => navigate('/propertyDashboard')}
                  class="text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm p-1.5 ml-auto inline-flex items-center darks:hover:bg-gray-600 darks:hover:text-white"
                  data-modal-hide="defaultModal"
                >
                  <svg
                    aria-hidden="true"
                    class="w-5 h-5"
                    fill="currentColor"
                    viewBox="0 0 20 20"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      fill-rule="evenodd"
                      d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                      clip-rule="evenodd"
                    ></path>
                  </svg>
                  <span class="sr-only">Close modal</span>
                </button>
              </div>
              <div class="p-6 space-y-6 hidden md:block">
                <p class="text-base leading-relaxed text-gray-500 darks:text-gray-400">
                  If you're looking to apply for property holding new assessment, there are few documents you'll need to gather beforehand. Typically, you'll need to provide proof of ownership or possession of the property, such as a deed or lease agreement. You may also need to submit a recent appraisal or assessment of the property's value
                </p>
                <p class="text-base leading-relaxed text-gray-500 darks:text-gray-400">
                  Other documents that may be required include your tax ID number, proof of income or financial statements, and any relevant permits or licenses. It's important to check with your local assessment office or jurisdiction to determine the specific requirements for your application. Once you've gathered all the necessary documents, you can submit your application and await the outcome of the assessment process.
                </p>
              </div>

              <div className="flex flex-col md:flex-row justify-center px-10 md:space-x-4">
                <div className="font-semibold">Please Select Your ULB</div>

                <div className="md:col-span-12 col-span-12 w-full md:w-1/3">
                  {!ulbList ? (
                    <div className="flex justify-center">
                      <ThreeDots
                        height="80"
                        width="80"
                        radius="9"
                        color="#5664e8"
                        ariaLabel="three-dots-loading"
                        wrapperStyle={{}}
                        wrapperClassName=""
                        visible={true}
                      />
                    </div>
                  ) : (
                    <select
                      onChange={(e) => {
                        props?.fetchMasterData(e.target.value)
                        setcurrentUlb(e.target.value)
                        props?.setchoosedUlbId(e.target.value)
                      }
                      }
                      name="ulb"
                      required
                      className="cypress_ulb w-full h-8 md:border md:border-gray-500 rounded-md px-2 text-base cursor-pointer"
                    >
                      <option value="">Select</option>
                      <option value="2">Akola Municipal Corporation</option>
                      {/* {ulbList?.map((item) => (
                        <option value={item?.id}>{item?.ulb_name}</option>
                      ))} */}
                    </select>
                  )}
                  {!validULB && (
                    <p className="text-red-400">Please Select ULB</p>
                  )}
                </div>
              </div>


              <hr className="mt-10" />
              <div class="flex justify-center items-center md:flex-row-reverse p-6  rounded-b">
                <button
                  onClick={actionUlbSelection}
                  disabled={selectedUlb.value == ""}
                  class={`cypress_ulb_start_button ${buttonActionSolid}`}
                >
                  START
                </button>
                <button
                  onClick={closeModal}
                  class={buttonCancelBorder}
                >
                  CANCEL
                </button>
              </div>
            </div>
          </div>
        </div>
      </CommonModal>
      {/* </Modal> */}
    </div>
  );
}

// ReactDOM.render(<App />, appElement);

export default SafWelcomeScreen;
