import React from 'react'
import Rate from '../../../../../Components/Media/rate.png'


function RentalRate2({data}) {
    return (
        <>
             <div className=''>
                <h1 className='px-2 font-semibold mt-4 text-gray-600 text-sm'><img className='inline w-6' src={Rate} alt="rate-image" /> Rental Rate 2</h1>

                <table className='min-w-full leading-normal mt-2'>
                    <thead className='font-bold text-left text-sm bg-indigo-600 text-white'>
                        <tr>
                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Construction Type</th>
                            <th className="px-2 py-3 border-b  text-xs capitalize text-left border-r border-l border-white">Principal Main Road</th>
                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Main Road</th>
                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">No Road</th>
                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Other</th>

                        </tr>
                       
                    </thead>
                    <tbody className="text-sm">

                        {
                           Object.entries(data)?.map(([key, value], index) => (
                            <tr className="bg-white shadow-lg border-b border-gray-200" key={index}>
                              <td className="px-2 py-2 text-sm text-left">{key}</td>
                              <td className="px-2 py-2 text-sm text-left">{value['Principal Main Road'][0]?.rate}</td>
                              <td className="px-2 py-2 text-sm text-left">{value['Main Road'][0]?.rate}</td>
                              <td className="px-2 py-2 text-sm text-left">{value['No Road'][0]?.rate}</td>
                              <td className="px-2 py-2 text-sm text-left">{value['Other'][0]?.rate}</td>
                            </tr>
                          ))
}

                    </tbody>
                </table>
            </div>
        </>
    )
}

export default RentalRate2