import React from 'react'
import Rate from '../../../../../Components/Media/rate.png'


function MultiFactor2({data}) {
    return (
        <>
            <div className='ml-20 h-[70vh]'>
                <h1 className='px-2 font-semibold mt-4 text-gray-600 text-sm'><img className='inline w-6' src={Rate} alt="rate-image" /> Multi factor 1</h1>

                <table className='min-w-full leading-normal mt-2'>
                    <thead className='font-bold text-left text-sm bg-indigo-600 text-white'>
                        <tr>
                            <th className="px-2 py-3 border-b border-gray-200  text-xs capitalize text-left">Usage Type</th>
                            <th className="px-2 py-3 border-b  text-xs capitalize text-left border-r border-l border-white">Multi Factor</th>

                        </tr>
                       
                    </thead>
                    <tbody className="text-sm">
                        <>
                        {
                           data?.map((elem, index) => (
                            <tr className="bg-white shadow-lg border-b border-gray-200" key={index}>
                              <td className="px-2 py-2 text-sm text-left">{elem?.usage_type}</td>
                              <td className="px-2 py-2 text-sm text-left border-l border-gray-500">{elem?.multi_factor}</td>
                            </tr>
                          ))
}                            
                        </>

                    </tbody>
                </table>
            </div>
        </>
    )
}

export default MultiFactor2